@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);


.album{padding:1rem;background:#fff}.album span{display:block}.album .album-number{font-size:0.875rem;color:var(--accent-reg);text-transform:uppercase;margin-bottom:0.5rem}.album .album-title{text-transform:capitalize;font-weight:bold;line-height:1.5}

.spinner-overlay{height:60vh;width:100%;display:flex;justify-content:center;align-items:center}.spinner{display:inline-block;width:50px;height:50px;border:3px solid var(--primary-30);border-radius:50%;border-top-color:var(--accent-reg);animation:spin 1s ease-in-out infinite}@keyframes spin{to{-webkit-transform:rotate(360deg)}}

.grid{display:grid;grid-gap:2rem;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr))}


.user{padding:1rem;cursor:pointer;transition:background-color 0.2s}.user:not(:last-of-type){border-bottom:1px solid var(--primary-10)}.user:hover{background-color:var(--primary-05)}.user.active{background-color:var(--accent-bkd);box-shadow:inset 5px 0 0 var(--accent-reg)}.user span{display:block}.user span:last-of-type{margin-top:0.125rem}.user .user-wrapper{display:flex}.user .avatar{width:65px;height:65px;margin-right:1rem;border-radius:50%}.user .user-details{color:var(--primary-70)}.user .user-name{margin-bottom:0.375rem;font-weight:600;font-size:0.95rem}

.users{width:100%;height:calc(100% - var(--nav-height));overflow-y:scroll;border-top:1px solid var(--primary-10)}

.sidebar{background:#fff;width:340px;height:100%;font-size:0.875rem;overflow:hidden;transition:margin-left 0.3s}.sidebar.hidden{margin-left:calc(0px - var(--sidebar-width))}.sidebar h2{font-size:1.5rem;height:var(--nav-height);display:flex;align-items:center;padding:0 1rem}@media (max-width: 800px){.sidebar{position:fixed;top:var(--nav-height);height:calc(100% - var(--nav-height));z-index:1}}

.home{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.home .home-content{max-width:500px;text-align:center;line-height:1.5}

.photo{width:100%}.photo img{width:100%}.photo figcaption{font-size:0.875rem;margin-top:0.25rem;text-transform:capitalize}

.photos h1{text-transform:capitalize}

.nav-button{cursor:pointer;padding:0.25rem}.nav-button.is-open .hamburger-bar{transition-delay:0.14s;transition-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1);transform:rotate(45deg)}.nav-button.is-open .hamburger-bar:before{top:0;transition:top 0.1s ease, opacity 0.1s 0.14s ease, background-color 0.2s;opacity:0}.nav-button.is-open .hamburger-bar:after{bottom:0;transition:bottom 0.1s ease,transform 0.1s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1);transform:rotate(-90deg)}.nav-button:focus{outline:none;box-shadow:0 0 0 1px #fff;border-radius:0.125rem}.nav-button .hamburger-btn{position:relative;width:25px;height:25px;display:flex;align-items:center}.nav-button .hamburger-bar,.nav-button .hamburger-bar:after,.nav-button .hamburger-bar:before{position:absolute;width:100%;height:1px;transition-timing-function:ease;transition-duration:0.15s;transition-property:transform;border-radius:1px;background-color:#fff}.nav-button .hamburger-bar{transition-timing-function:cubic-bezier(0.55, 0.055, 0.675, 0.19);transition-duration:0.1s;display:block}.nav-button .hamburger-bar:before{transition:top 0.1s 0.14s ease, opacity 0.1s ease;top:-8px}.nav-button .hamburger-bar:after{transition:bottom 0.1s 0.14s ease,transform 0.1s cubic-bezier(0.55, 0.055, 0.675, 0.19);bottom:-8px}.nav-button .hamburger-bar:before,.nav-button .hamburger-bar:after{display:block;content:""}

.nav-bar{width:100%;height:60px;background-color:var(--accent-reg);display:flex;align-items:center;padding:0.75rem;position:relative;z-index:2}

:root {
  /* Typography */
  --font-text: "Montserrat", Helvetica, sans-serif;

  /* Color Palette */
  --primary-100: #333943;
  --primary-90: #474c55;
  --primary-80: #5b6068;
  --primary-70: #70747b;
  --primary-60: #84888e;
  --primary-50: #999ca0;
  --primary-40: #adafb3;
  --primary-30: #c1c3c6;
  --primary-20: #d6d7d9;
  --primary-15: #eaebec;
  --primary-10: #f0f1f1;
  --primary-05: #f8f9f9;
  --accent-dark: #0f62a7;
  --accent-reg: #137ad1;
  --accent-bkd: #deecf8;

  /* Measurements */
  --sidebar-width: 340px;
  --nav-height: 60px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  color: inherit;
  text-decoration: none;
  vertical-align: baseline;
}

html,
body,
#root,
#app {
  height: 100%;
}

body {
  font-family: "Montserrat", Helvetica, sans-serif;
  font-family: var(--font-text);
  color: #333943;
  color: var(--primary-100);
  background-color: #f0f1f1;
  background-color: var(--primary-10);
  font-size: 16px;
}

h1 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.main-window {
  display: flex;
  height: calc(100% - 60px);
  height: calc(100% - var(--nav-height));
}

.content-window {
  padding: 2rem;
  overflow-y: auto;
  width: 100%;
  transition: width 0.3s;
}

.content-window.sb-open {
  width: calc(100% - 340px);
  width: calc(100% - var(--sidebar-width));
}

@media (max-width: 800px) {
  .content-window {
    position: fixed;
    top: 60px;
    top: var(--nav-height);
    left: 0;
    transition: left 0.3s;
    height: 100%;
  }
  .content-window.sb-open {
    width: 100%;
    overflow: hidden;
    left: 340px;
    left: var(--sidebar-width);
  }

  .content-window.sb-open:after {
    content: "";
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(71, 76, 85, 0.6);
  }
}

